.btnBox {
  display: flex;
  gap: 10px;
  justify-content: center;
  align-content: center;
  align-items: center;
  .btn {
    flex: 1;
  }

  &.fixed {
    position: fixed;
    bottom: 0;
  }
  &.fixed_v2 {
    position: fixed;
    bottom: 0;
    right: 0;
    width: 42.32%;
    box-sizing: border-box;
    padding: 0px 20px;
  }
  &_v2 {
    gap: 0px;
    flex-direction: column;
  }
  &_v3 {
    justify-content: space-between;
  }
}
.title {
  width: 100%;
  font-family: $agencyFB !important;
  font-size: 60px;
  transition: 0.3s ease;
  line-height: normal;
  border-bottom: solid 1px black;
  letter-spacing: 6px;
  text-align: center;
  padding-bottom: 10px;
  &_small_v1 {
    font-size: 32px; //32px
    letter-spacing: 1px;
  }
  &_small_v2 {
    font-size: 20px; //20px
    letter-spacing: 1px;
  }
  &_v2 {
    font-size: 76px;
  }
  &_v3 {
    font-size: 65px;
    font-weight: bold;
    letter-spacing: 2px;
    text-align: left;
  }
  &_v4 {
    font-size: 55px;
    font-weight: bold;
    letter-spacing: 2px;
    text-align: left;
  }
}
.sub_title {
  font-size: 35px;
}
.textWrap {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.titleBox {
  display: flex;
  justify-content: space-between;
  align-items: baseline;
}
.textBox {
  display: flex;
  flex-direction: column;
}

.text {
  font-size: 24px; //
  color: black;
  font-family: $gillsans;
  &_point {
    font-weight: bold;
  }
  &_v3 {
    font-size: 22px; //
    display: block;
  }
  &_v4 {
    font-size: 20px; //
  }
  &_v4Bold {
    font-size: 20px; //
    letter-spacing: 0.5px;
    font-weight: bold;
  }
  &_v5 {
    font-size: 18px;
    text-align: left !important;
  }
  &_v5Bold {
    font-size: 18px;
    font-weight: bold;
  }
  &_v6 {
    font-size: 16px;
  }
  &_v7 {
    font-size: 14px;
    line-height: normal;
  }
  &_v7Gray {
    font-size: 14px;
    color: $gray-color;
  }
  &_v8 {
    font-size: 12px;
    line-height: normal;
  }
  &_heightNormal {
    line-height: normal;
  }
  &_spacingNormal {
    letter-spacing: normal;
  }
}
.underline {
  text-decoration: underline !important;
}
.imgBox {
  transition: 0.3s ease;
  &_v2 {
    width: 100%;
    height: 498px;
  }
}

.itemBox {
  display: flex;
  flex-wrap: wrap;

  &_v1 {
    gap: 50px;
    justify-content: center;
  }
  &_v2 {
    gap: 20px;
    justify-content: space-between;
  }
}
.item {
  object-fit: cover;
  position: relative;
  left: 0px;
  top: 0px;
  transition: 0.3s ease;
  &_v1 {
    width: 340px;
    height: 340px;
    box-shadow: 0 0 0 1px rgba($color: $border-color, $alpha: 0.25),
      inset 0 0 0 1px rgba($color: $border-color, $alpha: 0.25);
    box-sizing: border-box;
    overflow: hidden;

    .btnBox {
      img {
        opacity: 0.15;
      }
    }
    .textBox {
      width: 100%;
      box-shadow: 0px 4px 4px rgba($color: #000, $alpha: 0.25);
      background-color: #fff;
      padding: 20px;
      box-sizing: border-box;
      border-top: solid 5px $border-color;
      position: absolute;
      bottom: -40px;
      left: 0px;
      opacity: 0;
      transition: 0.2s ease;
      gap: 20px;
      text-align: left;
    }
  }
  &_v1.hover {
    .textBox {
      bottom: 0px;
      opacity: 1;
      transition: 0.2s ease;
    }
    .icon {
      backdrop-filter: blur(2px);
      filter: blur(4px);
      transition: 0.2s ease;
      transform: scale(1.05);
    }
  }
  &_v2 {
    /*width: 260px;
            height: 295px;*/
    min-height: 295px;
    min-width: 230px;
    flex: 1;
    box-shadow: 2px 2px 7px 2px RGB(0 0 0 / 50%);
    border-radius: 5px;
    padding: 30px 8px;
    box-sizing: border-box;
    .text {
      text-align: center;
      &_v4 {
        text-align: left;
      }
    }
  }
}
.icon {
  // width: 100%;
  // height: 100%;
  width: 15px;
  height: 15px;
  margin-right: 5px;
  display: inline-block;
  background-size: 100%;
  background-repeat: no-repeat;
  object-fit: cover;
  text-align: center;
  // position: absolute;
  // left: 0px;
  // top: 0px;
}
.cont {
  width: 100%;
  height: 45px;
  display: inline-block;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  object-fit: cover;
  text-align: center;
}
$conts: (oke, ecre, zik, ezen);
@each $cont in $conts {
  .cont_#{$cont} {
    background-image: url(../../resources/images/icon/skillsIcon/cont_#{$cont}.svg);
  }
}
$icons: (
  ai,
  atl,
  axios,
  bit,
  css,
  excel,
  figma,
  git,
  html,
  jira,
  js,
  mui,
  notion,
  point,
  post,
  ps,
  react,
  scss,
  slack,
  swagger,
  ts,
  vue,
  word
);
@each $icon in $icons {
  .icon_#{$icon} {
    background-image: url(../../resources/images/icon/skillsIcon/icon_#{$icon}.svg);
    // transition: 0.2s ease;
  }
  // .icon_#{$icon}.hover{
  //      backdrop-filter: blur(2px);
  //     filter: blur(4px);
  // }
}

//네모 도형
.square-wrap {
  width: 170px;
  display: flex;
  gap: 2px;
  align-items: center;

  .text_v6Gray {
    margin-left: 6px;
  }
}
.square-box {
  width: 10px;
  height: 10px;
  box-sizing: border-box;
}
.square-full {
